<template>
  <div class="overview">
    <!-- 上游 -->
    <div class="upstream view" ref="upstream">
      <!-- 电机原材料 -->
      <div class="rawMaterials" ref="rawMaterials"></div>
      <!-- 电机零部件 -->
      <div class="motorParts"></div>
    </div>
    <div class="icon">双向箭头</div>
    <!-- 中游 -->
    <div class="midstream view">中间</div>
    <div class="icon">双向箭头</div>
    <!-- 下游 -->
    <div class="downstream view">右侧</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // echarts实例
      rawMaterials: null,

      echartData: [
        {
          name: "上游：电机原材料",
          value: '',
          label:{
            fontSize:32
          },
          children: [
            {
              name: "第二层",
              value: "",
              label:{
                fontSize:32,
              },
              children: [
                {
                  name: "金属材料",
                  value: 12,
                  label:{
                    fontSize:32
                  }
                },{
                  name:"硅钢片",
                  value:28
                }
              ],
            },
          ],
        },
      ],
    };
  },
  props: {},
  components: {},
  methods: {
    // 节点点击事件
    treeNodeClick(param) {
      console.log(param);
      if (param.event.target.culling === true) {
        return;
      } else if (param.event.target.culling === false) {
        let args = param.data; //当前节点及其节点的值
        let level = param.data.level; //当前节点得层级 eg："1-1-0"，可以通过level判断当前的层级，从而进行不同的操作
        this.textNodeClick(args, level);
        console.log(args, level);
      }
    },

    // 层次配置
    getLevelOption() {
      return [
        {
          itemStyle: {
            borderColor: "#105B7B",
            borderWidth: 10,
            gapWidth: 10,
            // color: "red"
          },
          upperLabel: {
            show: false,
          },
        },
        {
          itemStyle: {
            borderColor: "#105B7B",
            // borderWidth: 10,
            // gapWidth: 10,
            // color: "#1790CB",
          },
          emphasis: {
            //移入效果
            itemStyle: {
              // borderColor: "#33f6fb",
            },
          },
        },
        {
          colorSaturation: [0.35, 0.5],
          itemStyle: {
            borderWidth: 20,
            gapWidth: 20,
            borderColor: "#1790CB",
            color: "#24A9E0",
          },
        },
        {
          
        }
      ];
    },

    // echarts初始化
    initEcharts() {
      this.rawMaterials = this.$echarts.init(this.$refs.rawMaterials);
      let option = {
        series: [
          {
            type: "treemap",
            width: "100%",
            height: "100%",
            label: {
              show: true,
              formatter: "{b}\n{c}",
            },
            upperLabel: {
              show: true,
              height: 80,
            },
            itemStyle: {
              borderColor: "#fff",
            },
            levels: this.getLevelOption(),
            data: this.echartData,
            breadcrumb: {
              //是否显示面包屑
              show: false,
            },
            roam: false, //禁止缩放拖拽
            nodeClick: false, //点击时不做反应
          },
        ],
      };
      // 绘制基础图形
      this.rawMaterials.setOption(option);
      this.rawMaterials.on("click", this.treeNodeClick);
    },
  },
  computed: {},
  watch: {},
  mounted() {
    this.initEcharts();
    window.addEventListener("resize", this.rawMaterials.resize);
  },
  beforeDestroy() {
    // 移除页面监听
    window.removeEventListener("resize", this.rawMaterials.resize);
  },
};
</script>
<style scoped lang="less">
.overview {
  height: calc(100vh - 10vh - 6vw - 0.3rem - 32px);
  border: 1px solid orange;
  overflow-y: scroll;
  display: flex;
  .view {
    flex: 8;
    border: 1px solid skyblue;
  }
  .icon {
    flex: 1;
  }
  // 电机原材料
  .rawMaterials {
    height: 500px;
  }
}
</style>