        <div class="pngTitle">
          {{ pngTitle }}
        </div>
        <div class="breadcrumb">
          <!-- 面包屑 -->
          <a-breadcrumb separator=">">
            <a-breadcrumb-item>{{ pngTitle }}</a-breadcrumb-item>
            <a-breadcrumb-item href=""> 二级 </a-breadcrumb-item>
            <a-breadcrumb-item href=""> 三级 </a-breadcrumb-item>
            <a-breadcrumb-item>四级</a-breadcrumb-item>
          </a-breadcrumb>
          <!-- echarts -->
          <div class="myEcharts" ref="myEcharts"></div>
          <!-- silder滑块 -->
          <a-slider
            range
            :step="1"
            :default-value="[2000, 2020]"
            :min="2000"
            :max="2020"
            :dots="true"
            :tooltipVisible="true"
            :included="true"
            :marks="marks"
            tooltipPlacement="bottom"
            @change="onChange"
            @afterChange="onAfterChange"
          />
          <!-- 相关产品展示 -->
          <h2>相关产品</h2>
          <div class="tag">
            <a-tag v-for="(item, index) of tagData" :key="index">{{
              item
            }}</a-tag>
          </div>
        </div>
      </a-col>
      <!-- 右侧 -->
      <a-col :span="6" :offset="1">
        <div class="title">
          <div>{{ title }}</div>
          <p class="line"></p>
        </div>
        <div class="line"></div>
        <ul class="list">
          <el-scrollbar>
            <li
              v-for="(item, index) of list"
              :key="index"
              :class="index == 0 ? 'active' : ''"
              @click="showModal(index)"
            >
              <span class="square top1" v-if="index == 0"
                >TOP{{ index + 1 }}</span
              >
              <span class="square top2" v-if="index == 1"
                >TOP{{ index + 1 }}</span
              >
              <span class="square top3" v-if="index == 2"
                >TOP{{ index + 1 }}</span
