              <span class="square" v-if="index > 2">{{ index + 1 }}</span>
              <p class="li_title">{{ item.title }}</p>
              <p class="address">地址：{{ item.address }}</p>
              <p class="time">注册日期：{{ item.time }}</p>
              <div class="bottom"></div>
            </li>
          </el-scrollbar>
        </ul>
      </a-col>
    </a-row>
    <!-- 遮罩 -->
    <a-modal
      title=" "
      :visible="dialog.visible"
      :footer="null"
      :zIndex="99999999"
      width="65%"
      @cancel="handleCancel"
    >
      <div class="modalMeta">
        <div class="img">
          <img src="@/assets/img/avatar.jpg" alt="">
        </div>
        <div class="companyNew">
